'use client'
import SignOutBtn from '@/components/auth/SignOutBtn'
import { Button } from '@/components/ui/button'
import { Plus } from 'lucide-react'
import { Cloud } from 'lucide-react'
import React from 'react'

export default function Header({add}) {
  return (
    <header className='lg:mx-30 md:mx-20 mx-3 my-10 py-3 px-5 border rounded-full border-slate-300/50 backdrop-blur-md sticky top-10'>
        <div className='flex items-center justify-between'>
            {/* Logo */}
            <div className='flex items-center gap-3'>
                <Cloud className='text-primary font-bold' size={40}/>
                {/* <h1 className='text-xl font-mono'>{31} files saved üíÄ</h1> */}
            </div>

            <div className='flex items-center gap-3'>
                <Button onClick={add} className={'cursor-pointer'}>Drop File <Plus /></Button>
                <SignOutBtn variant={'outline'}/>
            </div>
        </div>
    </header>
  )
}
